function aa(){
  var pic = document.getElementById("pic");
  var inner = document.getElementById("inner");
  var li = document.getElementById("ul").getElementsByTagName("a");
  var prev = document.getElementById("prev");
  var next = document.getElementById("next");
  //index
  var index = 1;
  //
  prev.onclick = function(){
    //600
    animate(1882);
    //1
    if(index==1){
      index=3;
    }else{
      index--;
    }
    //
    showButton();
  }
  //
  next.onclick = function(){
    //600
    animate(-1882);
    //5
    if(index==3){
      index=1;
    }else{
      index++;
    }
    showButton();
  }
  //offset
  function animate(offset){
    //.inner  .inner
    inner.style.left = parseInt(inner.style.left) + offset + "px";
    //-3000-600px-600-3000px
    if(parseInt(inner.style.left) < -5646){
      inner.style.left = -1882 + "px";
    }
    if(parseInt(inner.style.left) > -1882){
      inner.style.left = -5646 + "px";
    }
  }
  //
  function showButton(){
    //aactive 
    for(var i=0;i<li.length;i++){
      if(li[i].className=="active"){
        li[i].className="";
        //
        break;
      }
    }
    //indexaactive
    li[index-1].className="active";
  }
  //
  for(var i=0;i<li.length;i++){
    //
    li[i].onclick=function(){
      //id
      var id = parseInt(this.getAttribute("id"));
      //index-600 
      var offset = (id-index) * -1882;
      //
      animate(offset);
      //index
      index = id;
      //
      showButton();
    }
  }
}
function cc(){
  var pic = document.getElementById("pic");
  var inner = document.getElementById("inner");
  var li = document.getElementById("ul").getElementsByTagName("a");
  var prev = document.getElementById("prev");
  var next = document.getElementById("next");
  var index = 1;
  var timer = null;
  //
  var timer2 = null;
  var state = false;
  prev.onclick = function(){
    if(state){
      return;
    }
    animate(1882);
    if(index==1){
      index=3;
    }else{
      index--;
    }
    showButton();
  }
  next.onclick = function(){
    if(state){
      return;
    }
    animate(-1882);
    if(index==3){
      index=1;
    }else{
      index++;
    }
    showButton();
  }

  function animate(offset){
    state = true;
    var time = 300;
    var interval = 10;
    var speed = offset/(time/interval);

    var newLeft = parseInt(inner.style.left) + offset;
    function go(){
      clearInterval(timer);
      if((speed < 0 && parseInt(inner.style.left) > newLeft) || (speed > 0 && parseInt(inner.style.left) < newLeft)){
        inner.style.left = parseInt(inner.style.left) + speed + "px";
        timer=setInterval(go,interval);
      }else{
        state = false;
        inner.style.left = newLeft + "px";
        if(parseInt(inner.style.left) < -5646){
          inner.style.left = -1882 + "px";
        }
        if(parseInt(inner.style.left) > -1882){
          inner.style.left = -5646 + "px";
        }
      }
    }
    go();

  }
  function showButton(){
    for(var i=0;i<li.length;i++){
      if(li[i].className=="active"){
        li[i].className="";
        break;
      }
    }
    li[index-1].className="active";
  }
  for(var i=0;i<li.length;i++){
    li[i].onclick=function(){
      var id = parseInt(this.getAttribute("id"));
      var offset = (id-index) * -1882;
      if(state){
        return;
      }
      animate(offset);
      index = id;
      showButton();
    }
  }
  // next .
  function play(){
    timer2 = setInterval(function(){
      next.onclick();
    },3000);
  }
  //
  function stop(){
    clearInterval(timer2);
  }
  //.pic
  pic.onmouseover = stop;
  pic.onmouseout = play;
  //
  play();
}
function ff() {


$(".adv_items li").each(function (index,ele) {
  var $index=0;
  $(ele).mouseenter(function () {
    $(".adv_items li").removeClass("tab_current");
    if(index%2==0){
      $index = index;
      $(ele).addClass("tab_current");
      $(".tab_items div").each(function (index,mele) {
        if($index === index){
          $(this).show();
        }else {
          $(this).hide();
        }
      });
    }


  });

});
}
module.exports = {
  close,
  aa,
  cc,
  ff
}



